.slider {
	&-desc-block {
		max-width: 500px;
		@media (max-width: 770px) {
			max-width: 400px;
		}
		@media (max-width: 640px) {
			max-width: 330px;
		}
		@media (max-width: 500px) {
			max-width: none;
		}
	}
	&-img img {
		max-height: 230px;
		@media (max-width: 900px) {
			max-height: 160px;
		}
		@media (max-width: 770px) {
			max-height: 130px;
		}
		@media (max-width: 640px) {
			max-height: 120px;
		}
		@media (max-width: 500px) {
			display: none;
		}
	}
	&-item {
		display: flex !important;
		justify-content: space-between;
		align-items: center;
		padding: 20px 30px;
		border-radius: 15px;
		overflow: auto;
		height: 250px;
		border-bottom-left-radius: 20px;
		border-bottom-right-radius: 20px;
		overflow: hidden;
		cursor: pointer;
		background-color: #4158d0;
		background-image: linear-gradient(43deg, #4158d0 0%, #c850c0 46%, #ffcc70 100%);
		&--2 {
			background-color: #ff3cac;
			background-image: linear-gradient(225deg, #ff3cac 0%, #784ba0 50%, #2b86c5 100%);
		}
		&--3 {
			background-image: linear-gradient(
				63.1deg,
				rgba(5, 23, 111, 1) 16.4%,
				rgba(24, 95, 240, 1) 64.5%
			);
		}
		&--4 {
			background-image: linear-gradient(
				91deg,
				rgba(72, 154, 78, 1) 5.2%,
				rgba(251, 206, 70, 1) 95.9%
			);
		}
		h3,
		p {
			color: #fff;
		}
		h3 {
			font-size: 30px;
			font-weight: 700;
			margin-bottom: 10px;
		}
		p {
			font-size: 20px;
			font-weight: 400;
		}
		@media (max-width: 900px) {
			height: 200px;
			h3 {
				font-size: 25px;
			}
			p {
				font-size: 15px;
			}
			padding: 10px 20px;
		}
		@media (max-width: 770px) {
			height: 160px;
			h3 {
				font-size: 20px;
			}
			p {
				font-size: 14px;
			}
		}
		@media (max-width: 640px) {
			height: 160px;
			h3 {
				font-size: 17px;
			}
			p {
				font-size: 13px;
			}
			padding: 10px;
		}
		@media (max-width: 500px) {
			height: 150px;
			h3,
			p {
				text-align: center;
			}
			h3 {
				font-size: 16px;
			}
		}
	}
	margin: 0 auto;
}

.slick {
	&-prev,
	&-next {
		display: none !important;
	}
}
