@import '../variables';

.button {
	display: inline-block;
	background-color: $blue;
	border-radius: 30px;
	padding: 10px 20px;
	min-width: 100px;
	text-align: center;
	cursor: pointer;
	transition: background-color $duration ease-in-out, border-color $duration ease-in-out;
	border: 1px solid transparent;
	@include noselect();

	&,
	span {
		color: #fff;
	}

	i,
	span,
	path,
	svg {
		transition: all $duration ease-in-out;
	}

	&:hover {
		background-color: darken($blue, 8%);
	}

	&:active {
		background-color: darken($blue, 12%);
		transform: translateY(1px);
	}

	&-login {
		width: 100%;
		margin-bottom: 10px;
	}

	&-link-reg {
		color: blue;
	}

	&--footer {
		font-size: 20px;
	}

	&--menu {
		font-size: 20px;
		margin-bottom: 20px;
	}

	&--circle {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 32px;
		height: 32px;
		min-width: 32px;
		padding: 0;
		border-width: 2px;
	}

	&--black {
		background-color: $black;

		&:hover,
		&:active {
			background-color: lighten($color: $black, $amount: 10);
		}
	}

	&--outline {
		background-color: #fff;
		border-color: $blue;
		&,
		span {
			color: $blue;
		}
		&:disabled {
			border-color: #a9a9a9;
			&:hover {
				background-color: #fff;
				svg {
					path {
						fill: #a9a9a9;
					}
				}
			}
			&,
			span {
				color: #a9a9a9;
			}
			svg {
				path {
					fill: #a9a9a9;
				}
			}
		}

		svg {
			path {
				fill: $blue;
			}
		}

		&:hover {
			background-color: $blue;

			&,
			span {
				color: #fff;
			}

			svg {
				path {
					fill: #fff;
				}
			}
		}

		&:active {
			background-color: darken($blue, 8%);
		}
	}

	&__delimiter {
		width: 1px;
		height: 25px;
		background-color: rgba(255, 255, 255, 0.25);
		margin-left: 14px;
		margin-right: 14px;
	}

	&--add {
		svg {
			margin-right: 2px;
		}

		span {
			font-weight: 600;
			font-size: 16px;
		}
		transition: 0.01s linear;
		&:hover {
			i {
				background-color: #fff;
				color: $blue;
			}
		}

		i {
			display: inline-block;
			border-radius: 30px;
			background-color: $blue;
			color: #fff;
			font-weight: 600;
			width: 22px;
			height: 22px;
			font-style: normal;
			font-size: 13px;
			line-height: 22px;
			position: relative;
			top: -1px;
			left: 5px;
		}
	}

	&-logout {
		background-color: rgb(196, 0, 0);
		&:hover {
			background-color: rgb(133, 0, 0);
		}
	}

	&--cart {
		display: flex;
		align-items: center;
		line-height: 23px;
		padding: 12px 25px;

		svg {
			margin-right: 8px;
			margin-bottom: 1px;
		}

		span {
			font-weight: 600;
			font-size: 16px;
		}
	}
}
