@import '../../scss/variables';

.header {
	position: relative;
	border-bottom: 1px solid $gray-line;
	padding: 40px 0;
	margin-bottom: 20px;
	background-color: #fff;
	border-radius: 10px;
	@media (max-width: 960px) {
		&__logo div p {
			font-size: 20px;
		}
	}
	&__btns {
		display: flex;
		gap: 10px;
		@media (max-width: 350px) {
			flex-direction: column;
		}
		&-right {
			display: flex;
			align-items: center;
			gap: 10px;
			justify-content: center;
			@media (max-width: 350px) {
				gap: 30px;
			}
		}
	}

	.container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@media (max-width: 960px) {
			flex-direction: column;
			gap: 20px;
		}
	}

	&__logo {
		display: flex;
		@media (max-width: 960px) {
			align-items: center;
			gap: 15px;
			div {
				text-align: center;
			}
			img {
				width: 70px;
			}
		}

		img {
			margin-right: 15px;
		}

		h1 {
			color: #181818;
			font-size: 24px;
			letter-spacing: 1%;
			text-transform: uppercase;
			font-weight: 800;
		}

		p {
			color: #7b7b7b;
		}
	}
}

.btn-avatar {
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	background-color: #fff;
	border: 2px solid #000;
	border-radius: 50%;
	padding: 10px;
	max-width: 50px;
	max-height: 50px;
	overflow: hidden;
	&-img {
		min-width: 50px;
		min-height: 50px;
	}
}

.popup-menu {
	position: absolute;
	overflow: hidden;
	background-color: #fff;
	z-index: 2;
	border: 1px solid #7b7b7b;
	border-radius: 10px;
	padding: 10px;
	top: 60px;
	right: -20px;
	min-width: 130px;
	&-wrap {
		position: relative;
	}
	&-item {
		transition: 0.2s linear;
		padding: 10px;
		cursor: pointer;
		border-radius: 10px;
		&:hover {
			background-color: #000;
			color: #fff;
		}
	}
	&-info-user {
		text-align: center;
		margin: 10px 0;
	}
}
