@import '../../scss/variables';

.categories {
	max-height: 350px;
	padding: 10px;
	z-index: 2;
	min-width: 250px;
	position: absolute;
	top: 65px;
	left: -50px;
	background-color: #ffffff;
	border: 1px solid #7f7f7f;
	border-radius: 10px;
	overflow: auto;
	max-width: 100%;
	&-main {
		width: max-content;
		position: relative;
	}
	ul {
		display: flex;
		flex-direction: column;
		gap: 10px;

		li {
			text-align: center;
			background-color: #f9f9f9;
			padding: 13px 30px;
			border-radius: 30px;
			font-weight: bold;
			cursor: pointer;
			transition: 0.1s ease-in-out;
			@include noselect();

			&:hover {
				background-color: darken(#f9f9f9, 2%);
			}

			&:active {
				transform: scale(0.98);
				background-color: darken(#f9f9f9, 5%);
			}

			&.active {
				background-color: #282828;
				color: #fff;
			}
		}
	}
}
